body {
  margin: 0;
  padding: 0;
  font-family: 'Arial', sans-serif;
  background: #f0f4f8;
}

.container {
  max-width: 700px;
  margin: 40px auto;
  padding: 20px;
  background: #ffffff;
  border-radius: 12px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.08);
}

h1, h2 {
  color: #222;
  margin-bottom: 12px;
}

input[type="text"] {
  padding: 10px;
  width: 65%;
  border-radius: 6px;
  border: 1px solid #ccc;
  font-size: 15px;
  margin-top: 5px;
}

button {
  padding: 10px 16px;
  margin-left: 10px;
  border: none;
  background-color: #007BFF;
  color: white;
  border-radius: 6px;
  cursor: pointer;
  font-weight: bold;
  transition: background-color 0.2s ease;
}

button:hover {
  background-color: #0056b3;
}

.weather-box {
  margin-top: 20px;
  padding: 15px;
  border-radius: 10px;
  background-color: #f5faff;
  border: 1px solid #cce5ff;
}

.weather-detail {
  margin: 6px 0;
  font-size: 16px;
  color: #333;
}

.error {
  color: red;
  margin-top: 10px;
  font-weight: bold;
}

.note {
  font-size: 12px;
  color: #777;
  margin-top: 5px;
}

ul {
  list-style: none;
  padding: 0;
}

li {
  padding: 10px 8px;
  border-bottom: 1px solid #eee;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

li button {
  background-color: #dc3545;
}

li button:hover {
  background-color: #a71d2a;
}
